import logo from './logo.svg';
import './App.css';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Backdrop, Chip, CircularProgress, Grid, Stack, Button, FormControl, Select, InputLabel, MenuItem } from "@mui/material";
import TextField from '@mui/material/TextField';
import { DropzoneAreaBase } from "material-ui-dropzone";
import FormData from 'form-data';

function App() {
  const [getMessage, setGetMessage] = useState({})
  const [value, setValue] = useState('');
  const [resText, setResText] = useState('');
  const [minL, setMinL] = useState('');
  const [maxL, setMaxL] = useState('');
  const [file, setFile] = useState(null);
  const [model, setModel] = useState('');
  const [path, setPath] = useState('');
  const data = require('./models.json');
  console.log(data)

  useEffect(() => {
    axios.get('http://localhost:5545/flask/hello').then(response => {
      console.log("SUCCESS", response)
      setGetMessage(response)
    }).catch(error => {
      console.log(error)
    })

  }, [])

  const handleChange = (event) => {
    setValue(event.target.value);
  }

  const handleMin = (event) => {
    setMinL(event.target.value);
  }

  const handleMax = (event) => {
    setMaxL(event.target.value);
  }

  const handleModel = (event) => {
    setModel(event.target.value)
    console.log(data.models.find(d => d.name === model).description)
    setPath(data.models.find(d => d.name === model).path)
  }

  const sendValue = async () => {
    const form = new FormData();
    form.append('text', value);
    form.append('minL', minL);
    form.append('maxL', maxL);
    form.append('model', model);
    form.append('path', path);
    const res = await axios.post('http://localhost:5545/summarize/plaintext', form, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    setValue(res.data['original'])
    setResText(res.data['summary'])
  }

  const sendFile = async () => {
    const form = new FormData();
    form.append('file', file);
    form.append('minL', minL);
    form.append('maxL', maxL);
    form.append('model', model);
    form.append('path', path);
    let res;
    if (file.type == 'text/plain') {
      res = await axios.post('http://localhost:5545/summarize/file', form, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
    } else {
      res = await axios.post('http://localhost:5545/summarize/pdf', form, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
    }
    setValue(res.data['original'])
    setResText(res.data['summary'])
  }

  const handleFile = async (event) => {
    setFile(event[0].file);
    //sendFile();
  }

  return (
    <div>
      <Grid container className="App" direction="row" spacing={2}>
        <Grid container item className="col1" direction="column" xs={5}>
          <Grid item>
            <TextField
              id="outlined-multiline-flexible"
              label="Original Text"
              multiline
              minRows={10}
              maxRows={30}
              value={value}
              onChange={handleChange}
              fullWidth
            />
          </Grid>
          <Grid container item direction="row">
            <Grid item xs={2}>
              <TextField
                id="outlined-multiline-flexible"
                label="minimum Length"
                value={minL}
                onChange={handleMin}
                fullWidth
              />
            </Grid>
            <Grid item xs={2}>
              <TextField
                id="outlined-multiline-flexible"
                label="maximum Length"
                value={maxL}
                onChange={handleMax}
                fullWidth
              />
            </Grid>
            <Grid item xs={2}>
              <FormControl fullWidth>
                <InputLabel> Model
                </InputLabel>
                <Select
                  value={model}
                  onChange={handleModel}>
                  {
                    data.models.map((d) => {
                      return <MenuItem value={d.name}>{d.name}</MenuItem>
                    })
                  }
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={2}>
              <Button variant="contained" onClick={sendValue}>Send Text</Button>
            </Grid>
            <Grid item xs={2}>
              <Button variant="contained" onClick={sendFile}>{resText ? 'Resend File' : 'Send File'}</Button>
            </Grid>
          </Grid>
          <Grid item xs={1}>
          <TextField
            id="outlined-multiline-flexible"
            label="Description"
            multiline
            minRows={3}
            maxRows={3}
            value={data.models.find(d => d.name === model)?.description}
            fullWidth
          />
          </Grid>
          <Grid item xs = {1}>
            <DropzoneAreaBase
              acceptedFiles={[".txt", ".pdf"]}
              dropzoneText={"Add a text file"}
              onAdd={handleFile}
              maxFileSize={10000000}
              filesLimit={1}
              showAlerts={["error"]}
            />
          </Grid>
        </Grid>
        <Grid container item className="col2" xs={5}>
          <TextField
            id="outlined-multiline-flexible"
            label="Summary"
            multiline
            minRows={10}
            maxRows={20}
            value={resText}
            fullWidth
          />
        </Grid>
      </Grid>
    </div>
  );
}

export default App;
